.search-result-searchbar-div {
    width:fit-content;
    margin: 20px;
    margin-bottom: 0;
    display: flex;
    flex-direction: row;
}
.search-result-google-div {
    margin-top: 15px;
    margin-left: 15px;
}
#what-day-search-result-list-div {
    display: flex;
    flex-direction: column;
    padding-left: 180px;
}
.horizontal {
    display: flex;
    flex-direction: row;
}
#schedule-div {
    width: 300px;
    border-left:solid rgb(0,0,0,0.2);
    margin: 20px;
}
#schedule-images-div {
    width: 110%;
    border-bottom:solid rgb(0,0,0,0.2);
}
.schedule-item {
    display: flex;
    flex-direction: row;
    padding: 20px;
    width: 100%;
}
.schedule-time {
    padding-right: auto;
}
.schedule-event {
    margin-left: auto;
}